      content: " ";
      z-index: 998;
      background-image: linear-gradient(to right, rgba(102, 102, 255, 0.15), rgba(179, 179, 255, 0.15) 80%, transparent 80%);
      background-size: 8.47458%;
      background-origin: content-box;
      background-clip: content-box;
      background-position: left top; }

.wrap-full {
  max-width: 100%;
  margin-left: auto;
